/* Basic Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Background with the new image */
body {
  font-family: 'Arial', sans-serif;
  background-image: url('https://api.time.com/wp-content/uploads/2016/09/weather-05.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

/* Main container styling */
.app {
  background: rgba(0, 0, 0, 0.6); /* Darker transparent background */
  padding: 40px;
  border-radius: 15px;
  text-align: center;
  max-width: 400px;
  width: 100%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Heading styling */
h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #ffeb3b; /* Brighter yellow color for the heading */
}

/* Input field styling */
input[type="text"] {
  width: 80%;
  padding: 12px;
  margin-bottom: 20px;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  outline: none;
}

/* Button styling */
button {
  background-color: #ff9800;
  border: none;
  color: white;
  padding: 12px 20px;
  font-size: 1rem;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

/* Button hover effect */
button:hover {
  background-color: #e68a00;
}

/* Weather info card */
.weather-info {
  background-color: rgba(255, 255, 255, 0.3); /* Light transparent background */
  padding: 20px;
  margin-top: 20px;
  border-radius: 10px;
  backdrop-filter: blur(10px);
}

.weather-info h2 {
  font-size: 1.8rem;
  margin-bottom: 10px;
  color: #ffeb3b; /* Same yellow color as heading */
}

.weather-info p {
  font-size: 1.1rem;
  margin: 5px 0;
  color: white;
}

/* Error message styling */
.error {
  color: #ffcccb;
  margin-top: 10px;
  font-size: 1rem;
}
